<template>
  <div style="overflow:hidden">
    <div class="containers">
      <img src="../../assets/contactus/bg-contact.png" alt="">
    </div>
    <div class="contact">
      <div class="container">
        <v-title :title="this.$t('m.contactUs')"></v-title>
        <div class="detail">
          <img src="../../assets/contactus/img-contact-1.png" alt="">
          <div class="right">
            <div class="name">Thai Fintech Group</div>
            <div class="address">
              <span>{{$t('m.address1')}}：</span>
              <span style="width:560px">{{$t('m.address')}}</span></div>
            <div class="phone">{{$t('m.phone')}}：02-1054607</div>
            <div class="fax">{{$t('m.fax')}}：02-1054688</div>
            <div class="otherway">
              <div class="title">{{$t('m.otherWay')}}</div>
              <div class="icon-group">
                <div><img src="../../assets/contactus/icon-facebook-grey.png" alt=""></div>
                <div><img src="../../assets/contactus/icon-twi-grey.png" alt=""></div>
                <div><img src="../../assets/contactus/icon-insta-grey.png" alt=""></div>
              </div>
            </div>
            <div class="item">
              <div class="name">Thai  Fintech {{$t('m.guangZhou')}}</div>
              <div class="address">
                <span>{{$t('m.address1')}}：</span>{{$t('m.guangZhouAddress')}}</div>
              <div class="phone">{{$t('m.phone')}}：020-366 2288</div>
              <div class="fax">{{$t('m.fax')}}：020-366 2288</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang='stylus'>
  @import '../../common/stylus/mixin.styl'
  .containers
    width 1200px
    margin 0 auto
    position relative
    height 438px
    img
      position absolute
      left -360px
  .contact
    padding 66px 0 112px 0
    .detail
      margin-top 22px
      img
        width 496px 
        height 352px
        vertical-align top
      .right
        vertical-align top
        display inline-block
        padding-left 44px
        width 634px
        .name
          margin-top 22px
          font-family: MicrosoftYaHei;
          font-size: 18px;
          font-weight: bold;
          text-align: left;
          color: #333333;
        .address
          margin-top 30px
          font-family: MicrosoftYaHei;
          font-size: 14px;
          text-align: left;
          color: #555555;
          span 
            display inline-block
            vertical-align top
        .phone,.fax
          margin-top 20px
          font-family: MicrosoftYaHei;
          font-size: 14px;
          text-align: left;
          color: #555555;
        .otherway
          margin-top 88px
          .title
            border none
            font-family: MicrosoftYaHei;
            font-size: 14px;
            line-height: 1.71;
            text-align: left;
            color: #555555;
          .icon-group
            margin-top 20px
            div
              width: 24px;
              height: 24px;
              background-color: #f3f3f3;
              border: solid 2px #555;
              border-radius 50%
              text-align center
              display inline-block
              margin-right 28px
              img
                width auto
                height auto
                vertical-align middle
        .item
          margin-top 88px
</style>


